.Question {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-content: center;
    align-items: center;
}
.Question .container {
    border-radius: 25px;
    width: 580px;
    background-color: rgba(255, 255, 255, 0.425);
}

.Question .container .question-quest {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: #fff;
}

.Question .container .question-quest h1 {
    margin-top: 30px;
    font-weight: bold;
}

.Question .container .question-quest h2 {
    margin: 10px 0;
    font-weight: 700;
}

.Question .container .question-choice {
    width: 100%;
    height: 100%;
    padding: 30px 0;
    display: flex;
    /* justify-content: space-around; */
    align-items: flex-start;
    flex-wrap: wrap;
}

.Question .container .question-choice > button {
    margin: 5px;  /* and that, will result in a 10px gap */
  }

.Question .container .question-choice button {
    /* width: calc(80%/2); */
    width: 280px;
    background-color: #b6ccfecb;
    height: 40px;
    border: none;
    color: white;
    font-weight: 500;
    border-radius: 15px;
    transition: all 0.8s;
}

.Question .container .question-choice button:hover {
    cursor: pointer;
    background-color: #859bcfcb;
}