.Start {
    margin: 0;
    width: 50%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-content: center;
    flex-direction: column;
    margin-left: 10%;
    color: white;
}

.Start .container h1 {
    font-weight: 700;
    font-size: 62px;
}

.Start .container h2 {
    margin: 15px 0 15px 8px;
    width: 50%;
    font-weight: 400;
}

.Start .container button {
    color: white;
    font-weight: 700;
    width: 20%;
    height: 40px;
    background-color: #B6CCFE;
    border: none;
    border-radius:25px;
    transition: all 0.3s;
}

.Start .container button:hover {
    cursor: pointer;
    border: 2px solid #B6CCFE;
}